.tickets {
  padding: 3em 1em;
  /* display: flex;
    flex-direction: column;
    align-items: center; */
  &__title {
    text-align: center;
    padding-top: 1em;
    text-transform: capitalize;
  }
  &__ul {
    margin: 0 auto;
    padding-top: 2em;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    @media (max-width: $tablet) {
      display: flex;
      width: 100%;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      li {
        border-left: none;
      }
    }
    li:first-of-type {
      border-left: none;
    }
    &__li {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      flex-wrap: wrap;
      width: 30%;
      border-left: solid 1px $light-grey;
      &_fee {
        display: flex;
        justify-content: center;
        height: 100px;
        font-size: 60px;
        text-transform: capitalize;
        span {
          padding-top: 20px;
          font-size: 10px;
        }
      }
      &_title {
        font-weight: lighter;
        padding-top: 1em;
      }
      &_text {
        font-size: x-small;
        padding-bottom: 1em;
        color: $grey;
      }
    }
  }
  &__btn {
    font-size: 0.6em;
  }
}
